
import { defineComponent } from 'vue';
import { mapActions, mapState } from 'vuex';

import Hotel from '@/entities/Hotel';

export default defineComponent({
	name: 'Hotels',

    computed: {
        ...mapState('hotels', [
            'hotels'
        ]),

        ...mapState('order', [
            'order'
        ])
    },

    methods: {
        ...mapActions('hotels', [
            'getHotels'
        ]),

        ...mapActions('order', [
            'selectHotel'
        ]),

        isSelectedHostel(id: Hotel['id']): boolean {
            return this.order.hotelId === id;
        },

        getItemClassName(id: Hotel['id']): Record<string, boolean> {
            const className = {
                'Hotels__item': true,
                'Hotels__item--selected': this.isSelectedHostel(id)
            };

            return className;
        },

        onItemClick(id: Hotel['id']): void {
            this.selectHotel(id);
            this.scrollToOrderForm();
        },

        scrollToOrderForm(): void {
            const form = document.querySelector('.OrderForm');

            if (form) {
                const nameInput = form.querySelector<HTMLInputElement>('.Input[name="name"]');

                if (nameInput) {
                    this.$nextTick(() => {
                        nameInput.focus();
                        form.scrollIntoView();
                    });
                }
            }
        }
    },

    created(): void {
        this.getHotels();
    }
});
