<template>
    <div class="Hotels">
        <h3 class="Hotels__title">
            Выберите отель
        </h3>

        <ul class="Hotels__list">
            <li
                v-for="{ id, name, image } in hotels"
                :key="id"
                :class="getItemClassName(id)"
                @click="onItemClick(id)"
            >
                <img class="Hotels__itemImage" :src="image" />

                <span class="Hotels__itemName">
                    {{ name }}
                </span>
            </li>
        </ul>
    </div>
</template>

<script lang="ts">
import { defineComponent } from 'vue';
import { mapActions, mapState } from 'vuex';

import Hotel from '@/entities/Hotel';

export default defineComponent({
	name: 'Hotels',

    computed: {
        ...mapState('hotels', [
            'hotels'
        ]),

        ...mapState('order', [
            'order'
        ])
    },

    methods: {
        ...mapActions('hotels', [
            'getHotels'
        ]),

        ...mapActions('order', [
            'selectHotel'
        ]),

        isSelectedHostel(id: Hotel['id']): boolean {
            return this.order.hotelId === id;
        },

        getItemClassName(id: Hotel['id']): Record<string, boolean> {
            const className = {
                'Hotels__item': true,
                'Hotels__item--selected': this.isSelectedHostel(id)
            };

            return className;
        },

        onItemClick(id: Hotel['id']): void {
            this.selectHotel(id);
            this.scrollToOrderForm();
        },

        scrollToOrderForm(): void {
            const form = document.querySelector('.OrderForm');

            if (form) {
                const nameInput = form.querySelector<HTMLInputElement>('.Input[name="name"]');

                if (nameInput) {
                    this.$nextTick(() => {
                        nameInput.focus();
                        form.scrollIntoView();
                    });
                }
            }
        }
    },

    created(): void {
        this.getHotels();
    }
});
</script>

<style lang="scss">
.Hotels {
    $root: &;

    &__title {
        text-align: center;
    }

    &__list {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-gap: 10px;
        margin-top: 10px;
    }

    &__item {
        position: relative;
        height: 200px;
        border: 1px solid transparent;
        transition: border-color 0.2s;

        &:hover {
            cursor: pointer;
        }

        &--selected {
            border-color: var(--yellow-color);

            #{$root}__itemName {
                color: var(--yellow-color);
            }
        }
    }

    &__itemImage {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__itemName {
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        padding: 5px 0;
        color: #FFFFFF;
        text-align: center;
        background-color: rgba(#000000, 0.55);
        transition: color 0.2s;

    }

    @media screen and (max-width: 968px) {
        &__list {
            grid-template-columns: repeat(3, 1fr); 
        }  
    }

    @media screen and (max-width: 764px) {
        &__list {
            grid-template-columns: 1fr 1fr; 
        }  
    }

    @media screen and (max-width: 576px) {
        &__list {
            grid-template-columns: 1fr;
        }
    }
}
</style>