import { randomItem } from '@/helpers';

import Hotel from '@/entities/Hotel';

const hotelImages = [
    'https://hotel-spb.ru/assets/components/phpthumbof/cache/room-business_002.788c4c20502cae038e66e118c369e7b7.jpg',
    'https://gkkiparis.ru/wp-content/uploads/2019/07/amd-190712-151320-98961-1024x683.jpg',
    'https://yaltaintourist.ru/upload/resize_cache/iblock/80f/1162_775_1/01112016-1.jpg',
    'https://lexx-crimea.ru/sites/default/files/inline-images/28_05_2019%20Shelyakin%20(139)_0.jpg',
    'https://cf.bstatic.com/images/hotel/max1024x768/219/219201723.jpg'
];

async function getHotels(): Promise<Response> {
    const hotels: Hotel[] = [];

    for (let i = 1; i <= 20; i++) {
        const hotel: Hotel = {
            id: `${i}`,
            name: `Отель ${i}`,
            image: randomItem(hotelImages)
        };

        hotels.push(hotel);
    }

    const jsonHotels = JSON.stringify(hotels);
    const response = new Response(jsonHotels);

    return response;
}

export { getHotels };