<template>
	<form class="OrderForm" @submit="onFormSubmit">
        <h3 class="OrderForm__title">
            {{ title }}
        </h3>

        <Input
            name="name"
            placeholder="Имя"
            :value="order.name"
            :disabled="!isSelectHotel"
            @input="onInputChange"
        />

        <Input
            name="surname"
            placeholder="Фамилия"
            :value="order.surname"
            :disabled="!isSelectHotel"
            @input="onInputChange" 
        />

        <Input
            name="email"
            placeholder="Email"
            :value="order.email"
            :disabled="!isSelectHotel"
            @input="onInputChange"
        />

        <Input
            name="phone"
            placeholder="Телефон"
            :value="order.phone"
            :disabled="!isSelectHotel"
            @input="onInputChange"
        />

        <Button :disabled="!isSelectHotel || !isOrderFull || isMade">
            Отправить заявку
        </Button>
    </form>
</template>

<script lang="ts">
import { defineComponent } from 'vue';
import { mapActions, mapState } from 'vuex';

import Input from '@/components/ui/Input.vue';
import Button from '@/components/ui/Button.vue';

export default defineComponent({
	name: 'OrderForm',

    components: {
        Input,
        Button
    },

    computed: {
        ...mapState('order', [
            'order',
            'isMade'
        ]),

        title(): string {
            return this.isMade ? 'Ваша заявка отправлена' : 'Заполните форму';
        },

        isSelectHotel(): boolean {
            return this.order.hotelId !== null;
        },

        isOrderFull(): boolean {
            const { name, surname, email, phone } = this.order;

            return name && surname && email && phone;
        }
    },

    methods: {
        ...mapActions('order', [
            'updateOrder',
            'makeOrder'
        ]),

        onInputChange(event: InputEvent): void {
            const input = event.target as HTMLInputElement;
            const name = input.name;
            const value = input.value.trim();

            this.updateOrder({ name, value });
        },

        onFormSubmit(event: Event): void {
            event.preventDefault();

            if (this.isOrderFull) {
                this.makeOrder();
            }
        }
    }
});
</script>

<style lang="scss">
.OrderForm {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 10px;
    width: 100%;
    max-width: 600px;
    margin: 30px auto 0;

    &__title {
        grid-column: span 2;
        text-align: center;
    }

    .Button {
        grid-column: span 2;
    }

    @media screen and (max-width: 576px) {
        grid-template-columns: 1fr;

        &__title {
            grid-column: span 1;
        }

        .Button {
            grid-column: span 1;
        }
    }
}
</style>