import { Module } from 'vuex';

import { State, Actions, Mutations, MutationTypes, UpdateOrderPayload } from '@/stores/order/types';
import { makeOrder } from '@/stores/order/mock';

import Hotel from '@/entities/Hotel';

const state: State = {
    order: {
        hotelId: null,
        name: '',
        surname: '',
        email: '',
        phone: ''
    },

    isMade: false
};

const actions: Actions = {
    selectHotel({ commit }, hotelId: Hotel['id']): void {
        commit(MutationTypes.SelectHotel, hotelId);
    },

    updateOrder({ commit }, payload: UpdateOrderPayload): void {
        commit(MutationTypes.UpdateOrder, payload);
    },

    async makeOrder({ commit, state }): Promise<void> {
        const response = await makeOrder(state.order);

        if (response.ok) {
            commit(MutationTypes.MakeOrder);
        }
    }
};

const mutations: Mutations = {
    selectHotel(state: State, hotelId: Hotel['id']): void {
        const { order } = state;

        if (order.hotelId === hotelId) {
            order.hotelId = null;
        } else {
            order.hotelId = hotelId;
        }
    },

    updateOrder(state: State, payload: UpdateOrderPayload): void {
        const { order } = state;
        const { name, value } = payload;

        order[name] = value;
    },

    makeOrder(state: State): void {
        state.order = {
            hotelId: null,
            name: '',
            surname: '',
            email: '',
            phone: ''
        };

        state.isMade = true;

        setTimeout(() => state.isMade = false, 2000);
    }
};

const store: Module<State, any> = {
    state,
    actions,
    mutations,
    namespaced: true
};

export default store;