import { Action, Mutation } from 'vuex';

import Hotel from '@/entities/Hotel';

enum ActionTypes {
    GetHotels = 'getHotels'
}

enum MutationTypes {
    SetHotels = 'setHotels'
}

type State = {
    hotels: Hotel[];
};

type Actions = {
    [ActionTypes.GetHotels]: Action<State, any>;
};

type Mutations = {
    [MutationTypes.SetHotels]: Mutation<State>;
};

export {
    State,
    Actions,
    ActionTypes,
    Mutations,
    MutationTypes
};