import { Action, Mutation } from 'vuex';

import Order from '@/entities/Order';

enum ActionTypes {
    SelectHotel = 'selectHotel',
    UpdateOrder = 'updateOrder',
    MakeOrder = 'makeOrder'
}

enum MutationTypes {
    SelectHotel = 'selectHotel',
    UpdateOrder = 'updateOrder',
    MakeOrder = 'makeOrder'
}

type State = {
    order: Order;
    isMade: boolean;
};

type Actions = {
    [ActionTypes.SelectHotel]: Action<State, any>;
    [ActionTypes.UpdateOrder]: Action<State, any>;
    [ActionTypes.MakeOrder]: Action<State, any>;
};

type Mutations = {
    [MutationTypes.SelectHotel]: Mutation<State>;
    [MutationTypes.UpdateOrder]: Mutation<State>;
    [MutationTypes.MakeOrder]: Mutation<State>;
};

type UpdateOrderPayload = {
    name: keyof Order;
    value: string;
};

export {
    State,
    Actions,
    ActionTypes,
    Mutations,
    MutationTypes,
    UpdateOrderPayload
};