import { Module } from 'vuex';

import Hotel from '@/entities/Hotel';

import { State, Actions, Mutations, MutationTypes } from '@/stores/hotels/types';
import { getHotels } from '@/stores/hotels/mock';

const state: State = {
    hotels: []
};

const actions: Actions = {
    async getHotels({ commit }): Promise<void> {
        const response = await getHotels();

        if (response.ok) {
            const hotels = await response.json() as Hotel[];
            
            commit(MutationTypes.SetHotels, hotels);
        }
    }
};

const mutations: Mutations = {
    setHotels(state: State, hotels: any): void {
        state.hotels = hotels;
    }
};

const store: Module<State, any> = {
    state,
    actions,
    mutations,
    namespaced: true
};

export default store;